.faq {
	padding-top: toRem(70);
	padding-bottom: toRem(70);
	@media (max-width: $tablet) {
		padding-top: toRem(50);
		padding-bottom: toRem(50);
	}

	// .faq__container

	&__container {
	}

	&__body {
		@media (min-width: $mobile) {
			background-color: var(--white-color);
			border-radius: toRem(20);
			border: toRem(1) solid rgba(0, 0, 0, 0.2);
			box-shadow: 0 0 toRem(22.7) 0 rgba(0, 0, 0, 0.1);
			padding: toRem(57) toRem(20);
			max-width: toRem(843);
			margin: 0 auto;
		}
	}

	&__wrapper {
		max-width: toRem(603);
		margin: 0 auto;
	}

	// .faq__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(27);
		}
		@media (max-width: $mobile) {
			text-align: left;
		}
		&.content {
			.content__title {
				font-weight: 700;
				&:not(:last-child) {
					margin-bottom: toRem(10);
				}
			}
			.content__text {
				@media (max-width: $mobile) {
					display: none;
				}
			}
		}
	}

	&__spollers {
	}
}

// .faq__spollers

.spollers {
	display: grid;
}
// Ð¡Ð¿Ð¾Ð¹Ð»ÐµÑ€
.spollers__item {
	&:first-child {
		border-top: toRem(3) solid var(--white-light-color);
	}
	border-bottom: toRem(3) solid var(--white-light-color);
}
// Ð—Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº ÑÐ¿Ð¾Ð¹Ð»ÐµÑ€Ð°
.spollers__title {
	font-size: toRem(18);
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: toRem(18);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(26) toRem(10) toRem(26) 0;
	list-style: none;
	font-weight: 600;
	line-height: 1.3;
	transition: all 0.3s;
	@media (max-width: $mobileSmall) {
		font-size: toRem(14);
		padding: toRem(23) toRem(10) toRem(23) 0;
	}
	@media (any-hover: hover) {
		&:hover {
			opacity: 0.6;
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}

	&._spoller-active {
		opacity: 1;
		@media (max-width: $mobileSmall) {
			padding-bottom: toRem(12);
		}
		.spollers__icon {
			transform: rotate(45deg);
		}
	}
}
.spollers__icon {
	flex: 0 0 toRem(12);
	width: toRem(12);
	height: toRem(12);
	transition: transform 0.3s;
	position: relative;
	top: toRem(6);
	transform-origin: 0 0;
	&::before,
	&::after {
		content: "";
		position: absolute;
		background-color: var(--brand-color);
		transform: translate(-50%, -50%);
		transition: all 0.3s;
	}
	&::before {
		width: 100%;
		height: toRem(1);
	}
	&::after {
		width: toRem(1);
		height: 100%;
	}
}

// ÐšÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ ÑÐ¿Ð¾Ð¹Ð»ÐµÑ€Ð°
.spollers__body {
	padding: toRem(0) toRem(0) toRem(25) toRem(0);
	line-height: 1.5;
	@media (max-width: $mobileSmall) {
		font-size: toRem(12);
		padding: toRem(0) toRem(0) toRem(18) toRem(0);
	}
}
