.hero {
	position: relative;

	// .hero__container

	&__container {
		display: grid;
		align-items: center;
		max-width: toRem(1300);
		margin: 0 auto;
		min-height: 100vh;
		@media (min-width: $mobile) {
			padding-top: toRem(30);
			padding-bottom: toRem(30);
		}
		@media (max-width: $mobile) {
			// padding-top: toRem(142);
			// padding-bottom: toRem(114);
		}
	}

	// .hero__content

	&__content {
		position: relative;
		z-index: 2;
		padding-top: toRem(30);
		padding-bottom: toRem(30);
		color: var(--white-color);
		&.content {
			.content__title_big {
				color: var(--white-color);
				&:not(:last-child) {
					margin-bottom: toRem(15);
					@media (max-width: $mobile) {
						margin-bottom: toRem(8);
					}
				}
			}
			.content__text {
				@include adaptiveValue("font-size", 20, 14);
				&:not(:last-child) {
					margin-bottom: toRem(20);
					@media (max-width: $mobile) {
						margin-bottom: toRem(10);
					}
				}
			}
			&_center {
				@media (max-width: $mobile) {
					display: flex;
					flex-direction: column;
					align-items: start;
					text-align: left;
				}
			}
		}
	}

	&__image {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		img {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
			@media (max-width: $mobileSmall) {
				object-position: 33%;
			}
		}

		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: var(--brand-color-light);
			z-index: 1;
		}
	}
}
