.footer {
	padding-top: toRem(239);
	padding-bottom: toRem(117);
	background-color: var(--primary-color);
	color: var(--white-color);
	position: relative;
	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.8) 100%);
		z-index: 1;
	}
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(60);
	}

	// .footer__container

	&__container {
		position: relative;
		z-index: 2;
	}

	// .footer__content

	&__header {
		@media (min-width: $mobile) {
			text-align: center;
		}
		&:not(:last-child) {
			margin-bottom: toRem(53);
			@media (max-width: $mobile) {
				margin-bottom: toRem(30);
			}
		}

		&.content {
			.content__title {
				max-width: toRem(819);
				margin: 0 auto;
				color: var(--white-color);
				&:not(:last-child) {
					margin-bottom: toRem(20);
					@media (max-width: $mobile) {
						margin-bottom: toRem(10);
					}
				}
			}

			.content__text {
				font-size: toRem(16);

				@media (max-width: $mobileSmall) {
					font-size: toRem(14);
				}
			}
		}
	}

	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		z-index: -1;
	}
}
