.about {
	// .about__container

	&__container {
		max-width: toRem(1300);
		margin: 0 auto;
		display: grid;
		gap: toRem(40);
		align-items: center;
		@media (min-width: $mobile) {
			grid-template-columns: 34% 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(10);
		}
	}

	// .about__image

	&__image {
		padding-bottom: percent(600, 428);
		border-radius: toRem(15);
		overflow: hidden;
		@media (max-width: $mobile) {
			order: 2;
			width: 63%;
			padding-bottom: 71.186916%;
			margin-left: auto;
		}
	}

	// .about__content

	&__content {
		position: relative;
		@media (min-width: $mobile) {
			padding: toRem(100) 0;
			&::before,
			&::after {
				content: "";
				position: absolute;
				width: 100vw;
				height: 100%;
				top: 0;
				background-color: var(--white-color-100);
				z-index: -1;
			}
			&::before {
				left: 0;
			}
			&::after {
				right: 0;
			}
		}

		&.content {
			.content__title {
				letter-spacing: toRem(-1);
				&:not(:last-child) {
					margin-bottom: toRem(10);
				}
			}

			.content__text {
				max-width: toRem(708);
			}
		}
		@media (max-width: $mobile) {
			order: 1;
		}
	}
}

