.testimonials {
	background-color: var(--white-color);
	padding-top: toRem(110);
	padding-bottom: toRem(41);
	@media (max-width: $mobile) {
		padding-top: toRem(50);
		padding-bottom: toRem(30);
	}

	// .testimonials__container

	&__container {
	}

	// .testimonials__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(32);
			@media (max-width: $mobile) {
				margin-bottom: toRem(10);
				text-align: left;
			}
		}
		&.content {
			.content__title {
				line-height: 1.2;
				&:not(:last-child) {
					margin-bottom: toRem(7);
				}
			}
		}
	}

	// .testimonials__body

	&__body {
	}

	// .testimonials__slider

	&__slider {
		padding: toRem(20);
		margin: 0 toRem(-20);
	}

	// .testimonials__wrapper

	&__wrapper {
	}
}

.testimonial-slide {
	padding: toRem(68) toRem(0) toRem(37) toRem(0);
	background-color: var(--white-color);
	box-shadow: 0 toRem(4) toRem(15) 0 rgba(0, 0, 0, 0.1);
	border-radius: toRem(15);
	display: flex;
	flex-direction: column;
	gap: toRem(42);
	@include adaptiveValue("padding-right", 98, 20, 0, $containerWidth, 991.98);
	@include adaptiveValue("padding-left", 32, 20, 0, $containerWidth, 991.98);
	@media (max-width: $tablet) {
		padding-top: toRem(40);
		padding-bottom: toRem(40);
	}
	@media (max-width: $mobile) {
		gap: toRem(20);
	}
	@media (max-width: $mobileSmall) {
		flex-direction: column-reverse;
		padding: toRem(20) toRem(22);
	}
	// .testimonial-slide__quote

	&__quote {
		line-height: 1.5;
		@media (max-width: $mobileSmall) {
			font-size: toRem(12);
		}
	}

	// .testimonial-slide__profile

	&__profile {
	}
}

.profile {
	@media (max-width: $mobileSmall) {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: toRem(20);
		&::after {
			content: "";
			flex: 0 0 toRem(30);
			width: toRem(30);
			height: toRem(30);
			background: url("@img/icons/quote.png") 0 0 / contain no-repeat;
		}
	}
	&__wrapper {
		display: flex;
		align-items: center;
		gap: toRem(20);
		@media (max-width: $mobileSmall) {
			gap: toRem(10);
		}
	}
	// .profile__avatar

	&__avatar {
		flex: 0 0 toRem(50);
		width: toRem(50);
		height: toRem(50);
		border-radius: 50%;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(40);
			width: toRem(40);
			height: toRem(40);
		}
	}

	&__body {
		display: flex;
		flex-direction: column;
		gap: toRem(5);
	}

	// .profile__name

	&__name {
		font-weight: 500;
		font-size: toRem(15);
		line-height: 1.5;
		@media (max-width: $mobileSmall) {
			font-size: toRem(14);
		}
	}

	// .profile__profession

	&__profession {
		font-size: toRem(14);
		color: var(--black-light);
		@media (max-width: $mobileSmall) {
			font-size: toRem(13);
		}
	}
}
